import SecureLS from "secure-ls";

const ls = new SecureLS({
    isCompression: false,
    encodingType: 'aes',
    encryptionSecret: 'm0u*3b3mGEPX',
});

export default {
    get(key) {
        return ls.get(key)
    },
    remove(key) {
        return ls.remove(key)
    },
    set(key, value) {
        return ls.set(key, value)
    },
    removeAll() {
        return ls.removeAll()
    },
}