<template>
  <div>
    <teleport to="body">
      <vue3-snackbar top center :duration="4000"></vue3-snackbar>
    </teleport>
    <ErrorRequestModal v-if="error" :closeFn="closeErrorModal" :message="message"/>
    <TheMenu />
    <LoadingScreenVue v-if="loading"/>
    <router-view></router-view>
  </div>
</template>

<script>
import TheMenu from "./components/TheMenu.vue";
import LoadingScreenVue from "./components/loading/LoadingScreen.vue";
import ErrorRequestModal from "@/components/modals/ErrorRequestModal/index.vue"
import store from "@/store";
import { mapActions } from "vuex";


export default {
  name: "App",
  components: {
    TheMenu,
    LoadingScreenVue,
    ErrorRequestModal
  },

  computed:{
    loading: function (){
      return store.getters['loading/isLoading']
    },
    error: function (){
      return store.getters['error/isError']
    },
    message: function (){
      return store.state.error.error
    }
  },

  methods:{
    ...mapActions('error', ['ActionSetError']),

    closeErrorModal(){
      this.ActionSetError(false)
    }
  }
};
</script>
