<template>
  <div class="item-form">
    <label>{{ label }}</label>
    <Select v-model="modelValue" @input="updateValue">
      <option
        v-for="(option, index) in optionsData"
        :value="option[fields[0]]"
        :key="index"
      >
        {{ option[fields[1]] }}
      </option>
    </Select>
  </div>
</template>
<script>
export default {
  name: "InputSelect",

  data() {
    return {
      optionsData: [],
    };
  },

  props: {
    label: String,
    options: {
      type: Object,
      default: [],
      requiered: true,
    },
    fields: {
      type: Object,
      default: ["id", "value"],
      requiered: false,

    },
  },

  beforeMount() {
    this.arrayToOject();
  },

  methods: {
    updateValue(event) {
      this.$emit("update:modelValue", event.target.value);
    },

    arrayToOject() {
      const id = this.fields[0];
      const valueKey = this.fields[1];
      if (typeof this.options[0] != "object") {
        this.optionsData = this.options.map((value, index) => {
          return { [id]: index + 1, [valueKey]: value };
        });
      }

      if (typeof this.options[0] == "object") {
          this.optionsData = this.options
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.item-form {
  display: grid;
  gap: 0.5rem;
}
</style>
