import * as types from "./mutation-types";


export const ActionSetClient = ({commit}, payload) => {
    commit(types.SET_CLIENT, payload)
}

export const ActionSetApplication = ({commit}, payload) => {
    commit(types.SET_APPLICATION, payload)
}

export const ActionSetService = ({commit}, payload) => {
    commit(types.SET_SERVICE, payload)
}

export const ActionSetRequest = ({commit}, payload) => {
    commit(types.SET_REQUEST, payload)
}



