export const ActionCheckToken  = ({dispatch}) => {
    const token = LocalStorageService.get('token');

    if(token && !store.state.auth.token){
        dispatch('ActionSetToken', token)
        return true;
    }

    return !!token || !!store.state.auth.token
}

export const ActionSetToken = ({commit}, payload) => {
    LocalStorageService.set('token', payload);
    commit(types.SET_TOKEN, payload)    
}

export const ActionLogout = ({commit}) => {
    LocalStorageService.removeAll();
    commit(types.SET_TOKEN, false)    
}