import InputPassword from '@/components/inputs/InputPassword.vue'
import prepareFormDataMixin from '@/mixins/prepareFormDataMixin';

import useValidate from '@vuelidate/core'
import { required } from '@vuelidate/validators'
import { mapActions } from 'vuex';

export default {
    name: 'ScreenLogin',
    components: {
        InputPassword,
    },

    mixins:[prepareFormDataMixin],

    data() {
        return {
            v$: useValidate(),
            form: {
                password: '',
            },
        }
    },

    validations(){
        return {
            form: {
                password: {required},
            },
        }
    },

    methods: {
        ...mapActions('auth', ['ActionSetToken','ActionCheckToken']),

        async login() {
            const validated = await this.v$.$validate();
            if(validated){
                
                this.ActionSetToken(this.form.password)
                this.$router.push('/requisicoes')
                return;
            }

            console.log('menssagem error');
        },

       

    },
}