import store from "@/store";
import {
    routesWithoutPermission
} from "./routesWithoutPermission";

export const routeRedirectLogged = (to) => {
    if (!store.getters['auth/isLogged']) {
        store.dispatch('auth/ActionCheckToken')
    }

    const isLogged = routesWithoutPermission.includes(to.path) && store.getters['auth/isLogged'];
    const notLogged = !routesWithoutPermission.includes(to.path) && !store.getters['auth/isLogged']
    const isAdmin = true //store.getters['auth/isAdmin'];

    if (notLogged || !to.matched.length) {
        return '/login';
    }

    if ((isLogged && isAdmin) || !to.matched.length) {
        return '/requisicoes';
    }

    if (isLogged || !to.matched.length) {
        return '/aplicacoes';
    }

    return false
}

export default routeRedirectLogged;