<template>
  <div class="area-info">

    <div class="row2">
      <p><strong>ID Requisição:</strong>{{ request.RowKey }}</p>
      <p><strong>Cpf: </strong>{{ request.cpf }}</p>
    </div>

    <div class="row2">
      <p>
        <strong>Data:</strong
        >{{ $filters.mask.get_date_of_datetime(request.created_at) }}
      </p>
      <p>
        <strong>Horário:</strong>
        {{ $filters.mask.get_time_of_datetime(request.created_at) }}
      </p>
    </div>

    <div class="row2">
      <p>
        <strong>Frente do Documento:</strong>
        <img :src="request.image_front" alt="Frente do documento" />
      </p>
      <p v-if="this.back.id">
        <strong>Verso do Documento:</strong>
        <img :src="request.image_back" alt="Verso do documento" />
      </p>
    </div>

    <div class="row2">
      <div class="row1" >
          <strong v-if="!front.show.text" @click="front.show.text = !front.show.text">Texto  &#9658 </strong>
          <strong v-if="front.show.text" @click="front.show.text = !front.show.text">Texto  &#9660 </strong>
          <p v-if="front.show.text">{{request.ocr.text?.[back.id]?.[0] }}</p>

          <strong v-if="!front.show.labels" @click="front.show.labels = !front.show.labels">Labels  &#9658 </strong>
          <strong v-if="front.show.labels" @click="front.show.labels = !front.show.labels">Labels  &#9660 </strong>
          <li class="list" v-if="front.show.labels">
            <ul v-for="(label,key) in request.ocr.labels?.[front.id]" :key=key>{{label}} </ul>
          </li>
          
          <strong v-if="!front.show.faces" @click="front.show.faces = !front.show.faces">Faces  &#9658 </strong>
          <strong v-if="front.show.faces" @click="front.show.faces = !front.show.faces">Faces  &#9660 </strong>         
          <li class="list" v-if="front.show.faces">
            <ul v-for="(face,key) in request.ocr.faces?.[front.id]?.[0]" :key=key>{{face}} </ul>
          </li>
        </div>

   
        <div class="row1" v-if="this.back.id" >
          <strong v-if="!back.show.text" @click="back.show.text = !back.show.text">Texto  &#9658 </strong>
          <strong v-if="back.show.text" @click="back.show.text = !back.show.text">Texto  &#9660 </strong>
          <p v-if="back.show.text">{{request.ocr.text?.[back.id]?.[0] }}</p>

          <strong v-if="!back.show.labels" @click="back.show.labels = !back.show.labels">Labels  &#9658 </strong>
          <strong v-if="back.show.labels" @click="back.show.labels = !back.show.labels">Labels  &#9660 </strong>
          <li class="list" v-if="back.show.labels">
            <ul v-for="(label,key) in request.ocr.labels?.[back.id]" :key=key>{{label}} </ul>
          </li>
          
          <strong v-if="!back.show.faces" @click="back.show.faces = !back.show.faces">Faces  &#9658 </strong>
          <strong v-if="back.show.faces" @click="back.show.faces = !back.show.faces">Faces  &#9660 </strong>         
          <li class="list" v-if="back.show.faces">
            <ul v-for="(face,key) in request.ocr.faces?.[back.id]?.[0]" :key=key>{{face}} </ul>
          </li>
        </div>

     
    </div>


  </div>
</template>

<script>
export default {
  name: "OcrDetails",

  props: {
    request: Array,
    closeFn: Function
  },
  

  mounted(){
    this.getImagesIds();
  },

  data(){
    return {
      front:{
        id:"",
        show:{
          text: false,
          labels: false,
          faces: false,
        },
      
      },
      back:{
        id:"",
        show:{
          text: false,
          labels: false,
          faces: false,
        }
      },
    }
  },

  methods:{
    getImagesIds(){
      const { image_front, image_back } = this.request;

      this.front.id = image_front.split('/')[4]
      this.back.id = image_back==undefined?null:image_back.split('/')[4]
     
      var ocr = this.request.ocr.replace(/'/g,'"')
      this.request.ocr = JSON.parse(ocr)      
    }
  }
};
</script>

