<template>
    <div class="breadcrumb">
        <ul>
            <li>
                <a  @click="$router.push(homePath)">
                    <img src="../assets/images/icon-home.svg" alt="Voltar para a tela inicial">
                </a>
            </li>
            <li v-if="paths != false" v-for="(path, key) in paths" :key="key">
                <a @click="$router.push(path.path)" :class="{ disabled_breadcrumb: path.path == $route.path }">
                    {{ path.name }}
                </a>
            </li>

        </ul>
    </div>
</template>

<script>
export default {
    name: 'Breadcrumb',
    props:{
        actualPath:{
            type: String,
            default: 'home'
        }
    },

    mounted(){
        const path = this.$route.path

        if(path == '/gestaodeclientes'){
            this.paths = false;
            return;
        }


        const actualPosition = this.paths.findIndex(value => value.path === path);
        
        if (actualPosition !== -1) {
            this.paths.splice(actualPosition + 1);
        }
        
    },

    data(){
        return {
            homePath: '/gestaodeclientes',
            paths:[

                {
                    name: "Aplicações",
                    path: "/aplicacoes"
                },

                {
                    path: "/servicos",
                    name: "Serviços",
                },

                {
                    path: "/requisicoes",
                    name: "Requisições",
                },
            ]
        }
    }
}
</script>

<style lang="scss" scoped>

.disabled_breadcrumb {
    color: var(--gray-3);
    cursor: default;
    pointer-events: none;
}
.breadcrumb {
    ul {
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding: 1rem 0;
        margin: 0;

        li {
            a {
                cursor: pointer;
                img {
                    height: 1.2rem;
                    margin-bottom: -0.3rem;
                }
            }
        }

        li::before {
            content: '/';
            padding: 0 1rem;
        }

        li:first-child::before {
            content: ' ';
            padding: 0;
        }

    }
}
</style>