import moment from 'moment';

export const cnpj = (value) => !value ? value : value.padStart(14, '0').replace(/^(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/, '$1.$2.$3/$4-$5')
export const cpf = (value) => !value ? value : value.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/, "$1.$2.$3-$4");

export const get_date_of_datetime  = (value) => (""+value).split(" ")[0]
export const get_time_of_datetime  = (value) => (""+value).split(" ")[1]
export const format_date = (value) => moment(String(value)).format('DD/MM/YYYY')

export const format_hour = (value) => moment(String(value)).format('HH:mm:ss')