<template>
  <div class="item-form">
    <label>{{ label }}:</label>

    <input v-if="!!mask"  v-maska :data-maska="mask" 
    :class="{'border-error': errors.length != 0}" 
    type="text" name="" id="" 
    :readonly="readonly"
    :value="modelValue" @input="updateValue" />

    <input v-if="!mask" 
    :readonly="readonly"
    :class="{'border-error': errors.length != 0}" 
    :type="type" :min="min" :max="max"
    name="" id="" 
    :value="modelValue" @input="updateValue" />
    
    
    
    <Transition name="fade">
      <div v-if="errors.length != 0" class="error-message">
        <small> {{ messageValidatorEnum[errors[0].$validator] }} </small>
      </div>
    </Transition>
  </div>
</template>
<script>
import messageValidatorEnumMixin from "@/mixins/messageValidatorEnumMixin"

export default {
  name: "InputText",
  props: {
    label: String,
    modelValue: "",
    readonly:false,
    min:{
      default:false
    },
    max:{
      default:false
    },
    type:{
      default:'text'
    },
    mask:{
      type: String,
      required: false,
      default:"",
    },
    errors: {
      default: [],
    },
  },

  mixins:[messageValidatorEnumMixin],


  methods: {
    updateValue(event) {
      this.$emit("update:modelValue", event.target.value);
    },
    getAtribbutes(){
      return {
        id:"",
        name: "",
        value: this.modelValue,
        type: 'text'
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.item-form {
  display: grid;
  gap: 0.5rem;
}
label::after{
  content: ' *';
  color: var(--red);
}

.error-message {
  display: flex;
  height: 0px;
  color: red;
}

.border-error{
  border-color: rgb(223, 11, 11);
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
