        <div class="center">
            <router-link to="/gestaodeclientes">
                <img src="../assets/images/logo-life4safe.svg" class="logosvg" alt="Logo Life4safe">
            </router-link>
            <input id="atv-menu" type="checkbox" v-model="checkbox"/>
            <label for="atv-menu">
                <div class="menu">
                    <span class="hamburger"></span>
                </div>
            </label>
            <ul class="list-menu">
                <!--
                <li>
                    <a href="https://live4safe-web-site.titcs-devops.com.br/" target="_blank">
                        Portal L4S
                    </a>
                </li>
                <li>
                    <a href="https://live4safe-web-site.titcs-devops.com.br/#values" target="_blank">
                        Planos
                    </a>
                </li>
                <li>
                    <a href="https://live4safe-web-site.titcs-devops.com.br/#contact" target="_blank">
                        Contatos
                    </a>
                </li>
                <li>
                    <a href="https://demo.live4safe.com.br/" target="_blank">
                        Acesse a Demo
                    </a>
                </li>
                -->
                <li>
                    <!--<button v-if="!isLogged" class="btn-secondary" @click.prevent="$router.push('/login')">Fazer Login</button>-->
                    <button title="Sair" v-if="isLogged" class="btn-logout" @click.prevent="logout()">
                        <span>Sair </span> 
                        <img src="../assets/images/icon-logout.svg" alt="">
                    </button>

                </li>
            </ul>
        </div>
    </header>
</template>
<script>
import { mapActions } from 'vuex';
import store from "@/store";


export default {
    name: 'TheMenu',
    components: {
    },
    data() {
        return {
            checkbox:false
        }
    },

    computed: {
        isLogged(){
        return  store.getters['auth/isLogged']
        }
    },

    methods:{
        ...mapActions('auth', ['ActionLogout']),

        logout(){
            this.ActionLogout();
            this.$router.push('/login')
        }
    }
}
</script>

<style lang="scss" scoped>
header{
    background-color: var(--white);
}
.center {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    align-items: center;
    margin: 0rem auto;
    padding: 2rem;

    .logosvg {
        height: 3rem;
    }

    .menu {
        border-radius: 50%;
        width: 2rem;
        height: 2rem;
        position: absolute;
        top: 2.5rem;
        right: 2rem;
        z-index: 3;

        .hamburger {
            display: block;
            background-color: var(--blue-dark);
            width: 1.6rem;
            height: 2.5px;
            position: relative;
            z-index: 12;
            margin: 1rem auto;
            border-radius: 3px;
        }

        .hamburger::before,
        .hamburger::after {
            background-color: var(--blue-dark);
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            transition: .5s linear;
            border-radius: 3px;
        }

        .hamburger::before {
            top: 0.5em;
        }

        .hamburger::after {
            bottom: 0.5em;
        }
    }

    input {
        display: none;
    }

    input:checked~nav {
        top: 3rem;
    }

    input:checked~label {
        .hamburger {
            transform: rotate(45deg);
        }

        .hamburger::before,
        .hamburger::after {
            transform: rotate(90deg);
            top: 0;
        }
    }
    .list-menu{
        display: grid;
        align-content: flex-start;
        text-align: center;
        justify-content: center;
        position: absolute;
        top: 5rem;
        left: 0;
        width: 100%;
        list-style: none;
        padding: 0rem;
        text-align: center;
        height: 0%;
        overflow: hidden;
        transition: .2s ease-in-out;
        z-index: 888;
        li{
            a{
                color: var(--black);
                transition: .1s ease-in-out;
                display: block;
                padding: 1rem 2rem;
                letter-spacing: 1px;
                font-family: 'Lato-SemiBold';
            }
            a:hover{
                color: var(--white);
                background-color: var(--blue);
            }
            button{
                display: flex;
                align-items: center;
                background-color: transparent;
                border: none;
                margin: 2rem auto 0;
                span{
                    font-size: 16px;
                    font-family: 'Lato-bold';
                    color: var(--red-dark);
                    margin-right: .5rem;
                }
                img{
                    height: 2rem;
                }
            }
        }
    }
    input:checked~.list-menu{
        background-color: var(--white);
        padding: 1rem 0;
        min-height: 100%;
        min-height: calc(100% - 8rem);

    }
}
@media screen and (min-width: '60em') {
    .center{
        justify-content: space-between;
        .menu{
            display: none;
        }
        .list-menu{
            position: static;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            height: auto;
            margin: 0;
            li{
                a{
                    padding: 1rem 2rem;
                }
                a:hover{
                    background-color: transparent;
                    color: var(--blue);
                }
                button{
                    margin-top: 0;
                }
            }
        }
    }
}
</style>