<template>
  <BaseModal
    :header="true"
    :title="this.algorithm[this.request.PartitionKey]"
    :isRetanguleActive="true"
    @close="closeFn()"
  >
   <LivenessDetails v-if="request.PartitionKey == 'liveness' && finish" :request="request" :details="details"/>

   <OcrDetails v-if="request.PartitionKey == 'ocr'" :request="request"/>

   <FacematchDetails v-if="request.PartitionKey == 'facematch'" :request="request" :details="details"/>

   <BackgroundCheckDetails v-if="request.PartitionKey == 'background_check' && finish" :request="request" :details="details"/>
    
  </BaseModal>
</template>

<script>
import BaseModal from "@/components/modals/BaseModal";
import LivenessDetails from "./contentDetails/livenessDetails/LivenessDetails.vue";
import OcrDetails from "./contentDetails/OcrDetails/OcrDetails.vue";
import FacematchDetails from "./contentDetails/facematchDetails/FacematchDetails.vue";
import BackgroundCheckDetails from "./contentDetails/backgroundCheckDetails/BackgroundCheckDetails.vue";

export default {
  name: "RequisitionsDetails",
  components: {
    BaseModal,
    LivenessDetails,
    OcrDetails,
    FacematchDetails,
    BackgroundCheckDetails,

  },
  props: {
    request: Array,
    closeFn: Function,
  },

  methods: {
    redirectToVideo(video) {
      window.open(video, "_blank");
    },
  },

  data() {
    return {
      finish: false,
      algorithm: { 'liveness': 'Prova de Vida' ,
                  'ocr': 'OCR de Documentos' , 
                  'facematch': 'Reconhecimento Facial',
                  'background_check': 'Background Check' },
    };
  },

  beforeMount() {
    this.finish = true;
  },
};
</script>


