import { isProxy, toRaw } from 'vue';

const prepareFormDataMixin = {
    methods: {
        prepareFormData: function (object) {
            const formData = new FormData();

            if (isProxy(object)){
                object = toRaw(object)
            }

            Object.keys(object).forEach((key) => {

                const value = object[key];

                if(typeof value == 'object'){
                    value.forEach((value, indexArray) => {
                        formData.append(`${key}[${indexArray}]`, value);
                    });

                    return;
                }

                formData.append(key, value);

            });

            return formData;
        }
    }
  }

  export default prepareFormDataMixin