import axios from "axios";
import store from "@/store"

const apiDemoLive = axios.create({
    baseURL: "https://demo-api.live4safe.com.br/",
});


apiDemoLive.interceptors.response.use(
    response => {
         return {error: false, data: response.data};
    },
    error => 
    {  
        
        const messagesDefault = {
            'get': ['Tente novamente mais tarde'],
            'delete': ['Objeto não pode ser exclúido caso exista dados inseridos nele'],
            'post':  ['Verifique os dados'],
            'patch':  ['Verifique os dados']
        } 

        if(typeof error.response.data == 'string'){
            let message = {'error': messagesDefault[error.response.config.method]}
            store.commit('error/ERROR/SET_ERROR',message)
            return Promise.reject({error: true, data: error.response.data}); 
        }

      
        if(error.response.data?.detail){
            store.commit('error/ERROR/SET_ERROR',{'error': [error.response.data.detail]})
            return Promise.reject({error: true, data: error.response.data}); 
        }
        store.commit('error/ERROR/SET_ERROR',error.response.data)
        
        return Promise.reject({error: true, data: error.response.data}); 
    }
);

export default apiDemoLive;
