<template>
    <!--
    <div class="area-dark">
        <div class="center">
            <div class="content">
                <p><b>Email:</b> {{ service.email }}</p>
                <p><b>Expira dia:</b> {{ service.expired_date }}</p>
                <p><b>Dias para retenção:</b> {{ service.retention_period }}</p>
            </div>
        </div>
    </div>-->
    <div class="light-area">
        <div class="center">
            <!--<h4>{{ `Serviço: ${service.username}` }}</h4>-->
            <div class="header-result">
                <span class="line"></span>
                <h2>Requisições</h2>
            </div>

            <div class="search">
                <div class="disp-flex">
                    <InputText label="Liveness ID" v-model="params.RowKey" />
                    <InputSelect label="Serviço" v-model="params.PartitionKey" :options="options.algorithm" />
                    <InputDate label="Período" @start_date="params.DateInit = $event"
                        @end_date="params.DateEnd = $event" />
                </div>
                <button class="btn-primary" @click="getRequests(false)"> Pesquisar </button>
            </div>

            <div class="center">

                <table v-if="requests.length > 0" class="results" aria-describedby="table results">
                    <thead>
                        <tr>
                            <th class="text-left">Liveness ID</th>
                            <th class="text-center">Criado</th>
                            <th class="text-center">Horário</th>
                            <th class="text-left">Modificado</th>
                            <th class="text-left">Horário</th>                            
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(request, key) in requests" :key="key">
                            <td class="top-card" data-label="Liveness ID">{{ request.RowKey }}</td>
                            <td class="text-left" data-label="Data">{{$filters.mask.format_date( $filters.mask.get_date_of_datetime(request.created_at) )}}
                            </td>
                            <td class="text-center" data-label="Horário">
                                {{ $filters.mask.get_time_of_datetime(request.created_at) }}</td>
                            <td class="text-left" data-label="Data">{{$filters.mask.format_date( $filters.mask.get_date_of_datetime(request.modified_at) )}}
                            </td>
                            <td class="text-center" data-label="Horário">
                                {{ $filters.mask.get_time_of_datetime(request.modified_at) }}</td>                            
                            <td class="text-right"><button @click.prevent="openDetails(request)">Detalhe</button></td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <button class="btn-primary btn-table" 
            v-if="this.params.nextrowkey" @click="getRequests(true)">Ver mais</button>
        </div>

        <RequisitionsDetails :request="request" v-if="show_modal" :closeFn="closeDetails" />
    </div>
</template>

<script>
import InputText from '@/components/inputs/InputText.vue';
import InputSelect from '@/components/inputs/InputSelect.vue';
import InputDate from '@/components/inputs/InputDate.vue';
import RequisitionsDetails from './RequisitionsDetails.vue';
import RequestService from '@/services/RequestService';
import { mapActions } from 'vuex';
import store from "@/store";
import Breadcrumb from '@/components/Breadcrumb.vue';


export default {
    name: 'RequisitionsIndex',
    components: {
        InputText,
        InputDate,
        RequisitionsDetails,
        InputSelect,
        Breadcrumb
    },
    methods: {
        ...mapActions('loading', ['ActionLoadingChange']),
        
        async getRequests(concat = true) {
            await this.ActionLoadingChange()

            if(!concat){
                this.params.nextpartitionkey=null
                this.params.nextrowkey= null
            }

            Object.keys(this.params).map((value) => {                
                if (this.params[value] == '') {
                    this.params[value] = null
                }
            })
            const response = await RequestService.getRequest(this.params);            
            this.params.nextpartitionkey = response[response.length-1]['nextpartitionkey']
            this.params.nextrowkey = response[response.length-1]['nextrowkey']
            response.pop()    
                   

            await this.ActionLoadingChange()

            this.requests = concat ? this.requests.concat(response) : response
        },

        openDetails(data) {
            this.show_modal = !this.show_modal
            this.request = data
        },
        closeDetails() {
            this.show_modal = false;
        },

        back() {
            return this.$router.push({ name: 'Serviços' })
        }
    },

    beforeMount() {
        this.service = store.getters['pagesData/service']
        if (Object.keys(this.service).length === 0) {
            return this.$router.push('/requisicoes')
        }
        this.getRequests();
    },

    watch: {
        'params.page'() {
            this.getRequests(false);
        }
    },
    data() {
        return {
            show_modal: false,
            requests: [],
            request: [],
            options: {

                algorithm: [
                    { 'id': 'liveness', 'value': 'Prova de Vida' },
                    { 'id': 'ocr', 'value': 'OCR de Documentos' }, 
                    { 'id': 'facematch', 'value': 'Reconhecimento Facial' },
                    { 'id': 'background_check', 'value': 'Background Check' },]
            },
            params: {
                PartitionKey: 'liveness',
                DateInit: null,
                DateEnd: null,
                RowKey: null,
                NumRows:35,
                nextpartitionkey: null,
                nextrowkey: null
            },
            
        }
    }
}
</script>

<style lang="scss" scoped>
.area-dark {
    background-color: var(--black);
    padding: 1rem 1rem 2rem;

    .content {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 3rem;
        grid-template-columns: 10rem 10rem;

        p {
            display: grid;
            color: var(--white);
            margin: 0;

            b {
                font-weight: normal;
                font-family: 'Lato-Bold';
                color: white;
            }

            span {
                display: inline-block;
                height: .5rem;
                width: .5rem;
                border-radius: 50%;
                margin: -.5rem 1rem 0 0;
            }

        }
    }
}

.light-area {
    padding: 1rem 0 5rem;
    background-color: var(--white);
    min-height: 100vh;
    min-height: calc(100vh - 9rem);

    .center {
        .center{
            padding: 1rem;
        }
        h4 {
            color: var(--blue);
            font-size: 1.5rem;
            margin: 1rem 1rem 2rem;
            text-align: left;
        }

        .box-info {
            background-color: var(--white-light);
            padding: .5rem 1.5rem;
            border-radius: .5rem;
            box-shadow: 0px 4px 8px 0px #00000040;
            margin: 0 1rem;

        }

        .search {
            background-color: var(--black);
            padding: 1.5rem;
            margin: 0 1rem;
            border-radius: .5rem;

            .item-form::v-deep {
                label {
                    color: var(--white);
                }

                span {
                    color: var(--white);
                }
            }

            .disp-flex {
                display: grid;
                gap: 1rem;
            }

            button {
                margin: 2rem auto 0;
                display: block;
                width: 20rem;
            }
        }

        .header-result {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
            margin: 2rem 1rem;
            position: relative;

            .line {
                display: block;
                height: .3rem;
                width: 18rem;
                background-image: linear-gradient(to right, #0C5FBA, transparent);
                border-radius: .5rem;
                position: absolute;
                top: 4.5rem;
            }

            h2 {
                color: var(--dark);
            }
        }

        .results {
            width: 100%;
            border-collapse: collapse;
            margin-top: 3rem;

            .text-left {
                text-align: left;
            }

            .text-center {
                text-align: center;
            }

            .text-right {
                text-align: right;
            }

            thead {
                background-color: var(--blue);
                color: white;
                text-transform: uppercase;
                font-size: .8rem;
                letter-spacing: 1px;
                padding: 1rem;
                height: 4rem;

                tr {
                    th {
                        padding: .5rem;
                    }
                }
            }

            tbody {
                tr {
                    td {
                        padding: 1rem .5rem;
                        position: relative;
                        &:hover::after{
                            content: attr(data-label);
                            position: absolute;
                            top: -1rem;
                            left: 50%;
                            transform: translateX(-50%);
                            width: 100%;
                            background-color: var(--white);
                            padding: 0rem;
                            border-radius: .5rem;
                            text-align: center;
                            color: var(--blue-dark);
                        }

                        button {
                            border: var(--black) solid 1px;
                            height: 2rem;
                            width: 6rem;
                            border-radius: 4px;
                            background-color: transparent;
                            transition: ease-in-out .1s;
                        }

                        button:hover {
                            background-color: rgba(0, 0, 0, 0.037);
                        }
                    }
                }

                tr:nth-child(even) {
                    background-color: var(--gray-7);
                }

            }
        }

        .btn-table {
            margin: 2rem auto;
            display: block;
            padding: 0 5rem;
        }
    }
}

@media screen and (min-width: '45em') {
    .light-area {
        .center {
            .box-info {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                gap: 2rem;

                p {
                    display: grid;
                }
            }

            .header-result {
                margin: 1rem 0 2rem;
            }

            .group-cards {
                grid-template-columns: repeat(2, 1fr);
            }
        }
    }

}

@media screen and (min-width: '60em') {
    .light-area {
        .center {
            .box-info {
                margin: 0;
            }

            .search {
                margin: 0;

                .disp-flex {
                    grid-template-columns: repeat(3, 1fr);

                    button {
                        height: 3rem;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 60em) {
    table {
        width: calc(100% - 2rem);

        tbody {
            background-color: transparent;
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 1rem;
        }

        thead {
            border: none;
            clip: rect(0 0 0 0);
            height: 1px;
            margin: -1px;
            overflow: hidden;
            padding: 0;
            position: absolute;
            width: 1px;
        }

        tr {
            display: block;
            background-color: var(--white-light) !important;
            padding-bottom: 1rem;
            width: 100% !important;
            border-radius: .5rem;
            overflow: hidden;

            td {
                padding: 0rem !important;
                min-height: 1.94rem;
            }

            td:nth-child(2) {
                text-align: left !important;
            }

            td:nth-child(3) {
                text-align: left !important;
            }

            td:nth-child(4) {
                text-align: left !important;
            }

            td:nth-child(5) {
                text-align: left !important;
                max-width: 50rem;
            }

            .text-right {
                text-align: center !important;

                button {
                    width: 10rem !important;
                }
            }
        }


        .sem-resultados {
            padding-top: 5rem;
        }

        td {
            display: block;
            min-height: 1.3rem;
            margin: 0.3rem 0.5rem;
            font-size: 0.9rem;

            img {
                height: 1.5rem;
                margin: 0rem 1rem;
            }
        }

        td::before {
            content: attr(data-label);
            float: left;
            font-weight: bold;
            color: var(--blue-extradark);
            margin-right: 0.5rem;
        }

        .top-card {
            background-color: var(--blue);
            margin: 0rem 0rem 1rem 0rem;
            font-weight: 600 !important;
            padding: 1rem !important;
            font-size: 0.9rem;
            color: white;
            letter-spacing: 1px;
            text-align: center !important;
        }

    }
}

@media screen and (max-width: 40em) {
    table {
        tbody {
            grid-template-columns: 1fr;
        }
    }
}</style>