import Login from "../views/Login";
import Requisitions from "../views/requisitions/RequisitionsIndex.vue";
import LoadingScreen from "../components/loading/LoadingScreen.vue";

const routes = [
    {
        path: "/login",
        name: "Login",
        component: Login,
    },
    {
        path: "/requisicoes",
        name: "Requisições",
        component: Requisitions
    },
    {
        path: "/loading",
        name: "Loading",
        component: LoadingScreen
    },
]

export default routes;